    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/lib/listlibqc",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data)
          this.libqcbLists=response.data.libqcb.data
          this.libqcbLists.forEach(item=>{
            this.$set(item,'_showDetails',false)
          })
          Number(response.data.libqcb.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.libqcb.total)
          this.$Loading.finish()
        })
      },
      submitNuqc(row){
        this.$http.post("/api/lib/submit_libqc",qs.stringify({libqc_type:'libqcb',order_id:row.item.order_id})).then(response=>{
          // console.log(response.data)
          this.getTableData()
        })
